/*Home Page*/

.home-container h2{
    font-size: 40px    
}

/*Banner Carousel*/

.home-container h2:nth-child(1){
    position: relative;
    top: 40px;
}

.banner-container{
    position: relative;
    margin-top: 10px;
    height: fit-content;
    padding: 50px;
    overflow: hidden;
}

.banner-container img{
    width: inherit;
    aspect-ratio: 2.2/1;
    object-fit: contain;
    border-radius: 20px;
}

.second-carousel{
    margin: 3% 7%;
    background-color: #fff;
    overflow: hidden;
    border-radius: 2rem;
    background: transparent;
}

.slide-items{
    min-height: 500px;
    min-width: 400px;
    padding: 40px;
}

.inner-slide-carousel{
    display: flex;
    background-color: #fff;
    cursor: grab;
    background: transparent;
}

.slide-images{
    height: 100%;
    width: 100%;
    border-radius: 10px;
    pointer-events: none;
}

/*Products Page*/

.product-page{
    position: relative;
}

.mens-product-container, .womens-product-container, .accessories-product-container{
    margin:20px;
}

.product-search{
    margin: 20px;
    width: 35%;
    height: 50px;
    border-radius: 20px;
}

.product-heading h4{
    font-size: 30px;
}

.card-img{
    transform: scale(1);
    transition: ease-in-out 0.3s;
    cursor: pointer;
}

.card-img:hover{
    transform: scale(1.2);
}

.card-title, .card-price{
    cursor: default;
}

.buy-btn{
    background-color: #F31559;
    margin: 5%;
}

.cart-btn{
    background-color: #0077b5;
}

/*About Us Page*/
.about-container{
    top: 10%;
    background-image: url(asserts/AboutUs/AboutUsCover.png);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 100% 100%;
    min-height: 100vh;
}

.about-details{
    margin: 7%;
    align-items: center;
    display: inline-block;
    width: 70%;
    object-position: center;
    left:50%;
}

.about-details h1{
    color: #0b8300;
}

.about-details h5{
    font-size: 30px;
}

.map-container{
    position:relative;
}

h2{
    color: #0b8300;
}




/*Footer styles*/

#footer{
    width: 100%;
    transform: translate(0,100%);
    box-shadow: 0 0 10px black;
    background-color: #A0C49D;
    font-size: larger;
    margin: 0;
}

.socialMedia svg{
    font-size: 35px;
    color: #fff;
    padding: 5px;
    margin: 1% 3%;
    cursor: pointer;
}

.instagram:hover{
    background: linear-gradient(orange, red);
    border-radius: 25%;
}

.facebook:hover{
    background: linear-gradient(blue, darkblue);
    border-radius: 25%;
}

.twitter:hover{
    background: white;
    color: #1da1f2;
    border-radius: 25%;
}

.linkedin:hover{
    background-color: white;
    color: #0077b5;
    border-radius: 25%;
}

#footer p{
    padding-bottom: 10px;
    color: #fff;
    pointer-events: none;
}

/*Contact Us Page*/

.contact-body h1{
    font-family: sans-serif;
    color: #367E18;
    padding: 20px;
    font-weight: bolder;
}

.contact-container{
    width: 100%;
    height: 100%;
    display: flex;
    direction: row;
}

.contact-left-container{ 
   width:50%;
   text-align: center;
   
}

.contact-right-container{
    width: 50%;
    text-align: center;
}

.contact-right-container h3{
    padding-top: 25px;
    justify-content: stretch;
}

.contact-right-container input{
    border-radius: 2rem;
    height: 2rem;
    width: 45%;
    font-size: 20px;
    text-align: center;
}

.envelope-svg{
    width: 300px;
    height: 300px;
    color: #367E18;
    
}

.contact-btn{
    border-radius: 10%;
    font-size: 15px;
    font-weight:bold;
    cursor: pointer;
    padding: 10px;
    border-color: #367E18;
    border-width: 3px;
}

.contact-btn:hover{
    color: white;
    background-color: #367E18;
}




