.navbar{
    padding-top: 30px;
    width:100%;
    height: 100%;
    background-color: #A0C49D;
    display: flex;
    direction: row;
}

.navbar .leftside{
    flex: 50%;
    height: 100px;
    display: flex;
    align-items: center;
    padding-left: 100px;
}

.navbar .rightside{
    flex: 50%;
    height: 100px;
    display: flex;
    align-items: center;
}

.navbar a {
    color: white;
    text-decoration: none;
    margin: 20px;
    padding:10px;
    border-radius: 25px;
    font-size: larger;
    font-weight: bold;
}

.nav-items:hover{
   background-color: white;
   padding: 10px;
   color: green;
   border:2px solid;
   border-radius: 25px;   
}


@media screen and (width <= 600px){
    .navbar .rightside {
        display: flex;
    }
}




/* Navbar Logo */
.logo{  
  border-radius: 50%;
  position: relative;
  left:30px;
  height: 150px;
  width: 150px;
}